        <h1 class="lh-lg">浙江省丽水市青田县鹤城</h1>
        <h2 class="lh-lg">一、简介</h2>
        <p class="text-start lh-lg">
          青田县辖镇，县府驻地。1949年建鹤城镇，1958年改公社，1961年复改镇。位于县境东南部，瓯江北岸。面积70.5平方公里，人口5.6万。330国道、金(华)温(州)铁路纵贯县境。瓯江水运发达。辖50个村委会和17个居委会。
        </p>
        <h2 class="lh-lg">二、著名景点</h2>
        <h3 class="lh-lg">中国石雕城</h3>
        <p class="text-start lh-lg">
          中国石雕城位于青田石雕文化发源地－青田县山口镇。山口镇是青田石雕的主产地，是全国最大的石雕、石章制作、销售、交易市场之一，是国内雕刻石的重要集散地。这里几乎家家办石雕厂，户户有石雕陈列室。石雕商店、作坊比比皆是。
          中国石雕城坐北朝南，由石雕市场和石雕精品馆两部分组分，占地面积18000多平方米，由8幢6层，砖混结构楼房和东西两侧起围合作用的厢房组成，建筑面积约20余万平方米。每幢建筑的底层作为石雕交易店铺和创作室，共有101间。
        </p>
        <h2 class="lh-lg">三、特产</h2>
        <h3 class="lh-lg">青田石</h3>
        <img src="@/assets/img/township/thc/qts.png" alt="青田石"><br><br>
        <p class="text-start lh-lg">
          产于浙江省青田县，我国传统的“四大印章石之一”。在中国一并与巴林石、寿山石和鸡血石被成为中国“[1]四大名石”。色彩丰富，花纹奇特。以“叶蜡石”为主，显蜡状，油脂、玻璃光泽，无透明、微透明至半透明，质地坚密细致，是中国篆刻用石最早之石种。
        </p>
        <h3 class="lh-lg">青田杨梅</h3>
        <img src="@/assets/img/township/thc/qtym.png" alt="青田杨梅"><br><br>
        <p class="text-start lh-lg">
          青田杨梅是浙江省丽水市青田县的特产。青田县杨梅具有质优、果大、味美等特点，色泽艳丽、甜酸适口，可生津止渴。2003年，青田县被命名为“中国杨梅之乡”。
          杨梅在青田有着悠久的栽培历史，据清光绪《青田县志》载，杨梅有红、紫、白三种，红胜于白、紫胜于红，产季窟（季窟寮）者佳；有下坑梅、魁市梅、茶山梅、黑炭梅等传统品种，其中魁市梅成熟早，下坑梅品质最佳，享有盛名。可见，光绪年间，青田农民种植杨梅已较为普遍，并具有一定的名气。
        </p><br><br>
